import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import TopBar from "../Components/TopBar";
import { theme } from "../style/theme";
import { Feed, FeedInfo } from "../Components/Feed";
import { useAppSelector } from "../app/hooks";
import { selectKakaoAccessToken } from "../features/counter/loginSlice";

function FeedPage() {
  const kakaoAccessToken = useAppSelector(selectKakaoAccessToken);
  const code = new URL(window.location.href).searchParams.get("code");

  useEffect(() => {
    if (!code || code.length <= 1) return;

    navigate("/login/oauth");
  }, []);

  const navigate = useNavigate();
  const f = new FeedInfo(
    "",
    "",
    "   20",
    "",
    new Date(),
    "https://img.khan.co.kr/news/2023/03/09/rcv.YNA.20230118.PYH2023011816770005100_P1.jpg"
  );
  return (
    <theme.style.page>
      <TopBar selectedPageName="" />

      <div> : {kakaoAccessToken}</div>

      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
      <Feed feedInfo={f} />
    </theme.style.page>
  );
}

export default FeedPage;
